<template>
    <div class="Book">
        <div class="video">
           <video id="myMovie" name="myMovie"
           :src="list[id].vUrl" 
           width="100%" height="100%" controls="" preload="auto" 
           :poster="list[id].img"></video>
        </div>
        <div class="img_list">
            <div class="img_list_top">
                <span>视频资料</span>
            </div>
            <div class="img_box clearfloat">
                <div v-for="(car,index) in list" :key="index" class="list_video" @click="goVideo(index)">
                    <img 
                    :src="car.img" 
                    width="100%" height="285">
                    <div class="text-bottom">{{car.name}}</div>
                    <img src="~@/assets/v_btn.png" class="v_btn" >
                </div>
            </div>
        </div>
		<div class="ul-box">
			<div class="img_list_top">
           		 <span>书籍资料</span>
        	</div>
			<div class="list clearfloat"  v-for="(car,index) in listBook" :key="index" @click="goPage(index+18)">
				<div class="left">
					<img :src="car.img">
				</div>
				<div class="right">
					<div class="title">{{car.title}}</div>
					<div class="text" v-html="car.text"></div>
				</div>
			</div>
    	</div>
  </div>
</template>
<script>
import Carousel from '@/components/Carousel'
export default {
  name: 'Book',
  components: {
    Carousel
  },
  data () {
    return {
        id: this.$route.query.id||0,
		list:[],
		listBook:[
		   {
			   img:require('../assets/acripts/14-y.jpg'),
			   title:"《翰墨情缘》",
			   text:"&nbsp;&nbsp;&nbsp;&nbsp;生于蓬莱仙境，此地一山分两海，渤海、黄海赋予慧根灵性；长于津沽名港，万船泊岸千樯待发,"
                +"帆篷海鸥骋目舒怀。少年豪侠，游于京华，仗笔如剑，步入画界，天赐机缘，得中国画大师齐白石先生赏识，"
                +"成为巨匠门中关门弟子，悉得大师秘笈真传。如此这般，既研习齐门“剑法”，又上溯青藤、八大、石涛、"
                +"虚谷众妙之门。寻门而入，破门而出，纵横捭阖，自成一格。<br/>——韩静霆"
                
		   },
		    {
			   img:require('../assets/acripts/15-y.jpg'),
			   title:"《生命的风景》吴冠中艺术专集",
			   text:"&nbsp;&nbsp;&nbsp;&nbsp;《生命的风景——吴冠中艺术专集》收集了吴冠中先生亲选的五百余幅油画、"
               +"水墨、速写和水彩精品，大部分作品配有吴冠中先生亲自撰写或由吴先生口述、"
               +"主编整理的图说，这些图说有作画的感受，作画的背景情形，有古今中外的艺术比较，"
               +"也有思古之幽情，开拓之甘苦；部分作品配有古今中外名家诗文。<br/>"
            　  +"&nbsp;&nbsp;&nbsp;&nbsp;从作品中不难看到，84岁高龄的吴冠中先生仍然以旺盛的创新热诚寻求着艺术创作的更高境界，"
                +"他宝刀不老，童心依旧，永远带给人们以热爱生命、热爱生活、热爱真善美的启示和力量。 <br/>"
            　　+"&nbsp;&nbsp;&nbsp;&nbsp;这套画集分四册，以年代划分，分为70年代精选作品、80年代精选作品、90年代精选作品（上）、"
                +"90年代精选作品（下），是吴冠中先生迄今最为完整的一套个人艺术作品专集。序言作者是美籍著名科学家李政道先生。"
		   },
		    {
			   img:require('../assets/acripts/16-y.jpg'),
			   title:"《写意人生》许麟庐",
			   text:"&nbsp;&nbsp;&nbsp;&nbsp;《写意人生许麟庐》内容简介：许麟庐先生虽然毕业于商学院，却对经商毫无兴趣，反而对绘画痴迷不已。他一生得名师、广交游。1939年，许先生在天津结识了溥心畲，并与这位当时首屈一指的画界泰斗结为忘年交，得到他在绘画、书法理念和技艺等方面的指点。"
		   },
           {
			   img:require('../assets/acripts/17-y.jpg'),
			   title:"许麟庐画集",
			   text:"1916年10月19日，农历的9月23，蓬莱仙境中的大皂许家村，出了一件大喜事儿。祖祖辈辈以渔为生的许树亭老爷子，得了个大胖小子。据说生下来就爱笑，这可高兴死了老奶奶： “瞧这小东西的，多希罕儿人，就叫‘骆驼’吧！” 只是因为“笑”、“乐”同义，“乐”、“骆”谐音，为图个吉祥如意，求个长命百岁，老师得了这么个乳名，却也早早预示了后来，这位世纪老人任重道远，高义薄云的一辈子。 这可是老奶奶包括老师自己都没想到的。"
                    +"也许，这就是人们经常说的“命”或是“天意”吧。<br/>"
                    +"——王鸿勋"
		   }
	   ]
    }
  },
  methods:{
	  pushList(){
          var name = ['许化迟：我家老爷子','2008年奥运会宣传片','渤海--许麟庐',
          '2010年北京画院画展','许麟庐画展纪实',
          '和平艺苑环境视频','2003年采访许化迟先生','竹箫斋里画许翁','国画大师《许麟庐》','央视《大家》栏目']
          var url = ['http://p.ivideo.sina.com.cn/video/251/212/535/251212535.jpg',
          'http://www.peaceartco.com/shipin/mp4/sp01_08aoyunhpyy.png',
          'http://www.peaceartco.com/shipin/mp4/sp02_bohaixll.png',
          'http://www.peaceartco.com/shipin/mp4/sp03_10xllhuazhanCCTVxw.png',
          'http://www.peaceartco.com/shipin/mp4/sp04_04xllsdhuazhanjsp.png',
          'http://www.peaceartco.com/shipin/mp4/sp05_15hpyyxcp.png',
          'http://www.peaceartco.com/shipin/mp4/sp06_03qgycaifangxhc.png',
          'http://www.peaceartco.com/shipin/mp4/sp07_zxzlhxw.png',
          'http://www.peaceartco.com/shipin/mp4/sp09_guohuadashi.png',
          'http://www.peaceartco.com/shipin/mp4/sp08_dajia.png']
          var vUrl = ['http://ask.ivideo.sina.com.cn/v_play_ipad.php?vid=142245604&amp;tags=h5_jsplay',
          'http://www.peaceartco.com/shipin/mp4/sp01_08aoyunHPYY.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp02_bohaixll.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp03_10xllhuazhanCCTVxw.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp04_04xllsdhuazhanjsp.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp05_15hpyyxcp.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp06_03qgycaifangxhc.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp07_zxzlhxw.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp09_guohuadashi.mp4',
          'http://www.peaceartco.com/shipin/mp4/sp08_dajia.mp4']
          for(var i = 0;i<name.length;i++){
              this.list.push(
                  {
                      img:url[i],
                      name:name[i],
                      vUrl:vUrl[i]
                  }
              )
          }
      },
       goPage(type){
           this.$router.push({path:'/AcriptsDetails',query:{id:type}});
      },
      goVideo(index){
            this.$router.push({path:'/Book',query:{id:index}}); 
            window.location.reload();
      }
  },
  mounted(){
   this.pushList();
  }
}
</script>
<style lang="less" scoped>
  .video{
       width: 1170px;
      margin: 0 auto;
  }
  video{
      background: #000;
  }
    .img_list{
      width: 1170px;
      margin: 0 auto;
  }
  .img_list_top{
      padding: 12px 0;
      text-align: center;
      font-size: 20px;
    color: #79242F;
       border-top: 2px solid  #79242F;
    border-bottom: 2px solid  #79242F;
    margin-bottom: 20px;
     position: relative;
    .know{
        position: absolute;
        right: 0;
        top:18px;
        font-size: 10px;
        color: #79242F;
    }
  }
  .img_box{
     
      img{
          display: block;
      }
      div{
          width: 380px;
          float: left;
          margin-bottom: 10px;
          margin-right: 15px;
          
      }
      .v_btn{
          position: absolute;
          margin: auto;
          left: 0;
          right: 0;
          bottom: 0;
          top: 0;
          width: 65px;
          height: 65px;
          background: none;
      }
      .list_video{
           cursor: pointer;
           position: relative;
      }
      div:nth-child(3n){
          margin-right: 0;
      }
      .text-bottom{
          height: 40px;
         background: #EFCAA9;
         line-height: 40px;
         text-align: center;
         font-size: 18px;
        color: #79242F;
      }
  }
  .ul-box{
	margin:  0 auto;
	width: 1170px;
	.left{
		width: 30%;
		height: 300px;
		float: left ;
        cursor: pointer;
        text-align: center;
        background: #000;
		img{
			max-width: 100%;
            height: 100%;
            object-fit: contain;
		}
	}
	.right{
		width:70%;
		height: 300px;
		float: left ;
		padding: 10px;
		background: #EBC7A7;
		cursor: pointer;
		.title{
			font-size:20px;
			color: #79242F;
			margin-bottom: 10px;
		}
		.text{
			font-size:16px;
			color: #333333;
			line-height: 30px;
			margin-bottom: 30px;
		}
		span{
			font-size: 15px;
			color: #79242F;
		}

	}
}
  .list{
	  margin-bottom: 20px;
  }
</style>


